.build {
  padding-top: 4rem;
  padding-bottom: 9rem;
  &__title {
    margin-bottom: 3rem;

    span {
      transform: rotate(-2deg) translateY(1rem);
    }
  }

  &__subtitle {
    margin-bottom: 7rem;
    max-width: 60rem;
  }

  &__items {
    border-top: 1px solid rgba(0, 0, 0, 0.2);

    display: flex;
    gap: 2.5rem;
    padding-right: 10rem;
    margin-bottom: 6.6rem;
  }
  &__item {
    flex-grow: 1;

    &:nth-child(1) {
      width: 100%;
      max-width: 32.4rem;
    }
  }
  &__icon {
    position: relative;
    width: 5.5rem;
    aspect-ratio: 1 / 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    border-radius: 100%;
    background-color: #f5f5f5;
    border: 1px solid rgba(29, 27, 26, 0.2);
    transform: translateY(-50%);
    img {
      height: 100%;
      width: 100px;
      object-fit: contain;
    }
    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: 0;
      width: 1.5rem;
      aspect-ratio: 1 / 1;
      background-image: url("./../img/cross.svg");
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
    }
    svg {
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    color: rgba(29, 27, 26, 0.8);
    margin-top: -1rem;
  }
  &__slider {
    .swiper-wrapper {
      transition-timing-function: linear !important;
    }
  }
  &__video {
    background-color: #1d1b1a;
    height: 23rem;
    width: 43.8rem;
  }
}
